<template>
	<div class="index">
		<div class="btn">
			<router-link :to="{ path:'question', query: {level: activeLevel} }">开始</router-link>
		</div>
		<div class="levels">
			<div class="level-block" :class="{'active': activeLevel === 0}" @click="selectLevel(0)">
				level1
				<div class="select-icon" :class="{'active-icon': activeLevel === 0}">✔</div>
			</div>
			<div class="level-block" :class="{'active': activeLevel === 1}" @click="selectLevel(1)">
				level2
				<div class="select-icon" :class="{'active-icon': activeLevel === 1}">✔</div>
			</div>
			<div class="level-block" :class="{'active': activeLevel === 2}" @click="selectLevel(2)">
				level3
				<div class="select-icon" :class="{'active-icon': activeLevel === 2}">✔</div>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	data () {
		return {
			activeLevel: 0
		};
	},
	created () {
		this.$root.eventHub.$on('close', () => {
			this.questions = false;
		});
	},
	methods: {
		showQuestions () {
			this.questions = true;
		},
		selectLevel (level) {
			this.activeLevel = level;
		}
	}
};
</script>

<style lang="stylus" scoped>
.index
	position: absolute
	left: 0
	top: 0
	height: 100%
	width: 100%
	.btn
		user-select: none
		cursor: pointer
		display: block
		width: 200px
		height: 100px
		line-height: 100px
		text-align: center
		margin: 50px auto 20px auto
		border: 1px dashed #999
		border-radius: 10px
		font-size: 36px
		a
			text-decoration: none
			color: #000
	.levels
		user-select: none
		cursor: pointer
		display: flex
		justify-content: space-around
		width: 100%
		margin: 0 auto
		.level-block
			position: relative
			flex: 0 0 auto
			width: 100px
			height: 40px
			line-height: 40px
			text-align: center
			font-size: 16px
			font-weight: 300
			border: 1px solid #aaa
			border-radius: 5px
			.select-icon
				position: absolute
				right: 0
				bottom: 0
				height: 16px
				width: 16px
				line-height: 16px
				background-color: #aaa
				font-size: 12px
				color: #fff
				border-radius: 4px
				&.active-icon
					background-color: red
			&.active
				border-color: red
</style>



